// Import only the necessary from 'vue-router'
import { createRouter, createWebHistory } from 'vue-router';

// Helper function for dynamic imports (lazy-loading)
function loadView(view) {
  return () => import(`@/views/${view}.vue`);
}
import middleware from "@/middleware/auth.js";

const routes = [
  {
    path: '/',
    component: loadView('IndexVue'), // Assuming 'IndexVue.vue' is the correct file name
    children: [
      { path: '', redirect: '/universityprofile' },
      { path: '/universityprofile', name: 'UniversityProfile', component: loadView('profile/UniversityProfile') },
      { path: '/dashboard', name: 'Dashboard', component: loadView('dashboard/Dashboard') },
      { path: '/help-and-support', name: 'HelpAndSupport', component: loadView('HelpAndSupport') },
      { path: '/chats/view', name: 'ChatDetails', component: loadView('chats/viewChats'), props: true },
      { path: '/chats/:chatOptions?', name: 'ChatMessage', component: loadView('chats/ChatMessage') },
      { path: '/chats/newmessage', name: 'ChatBox', component: loadView('chats/ChatBox') },
      { path: '/favouritemessage', name: 'FavouriteMessage', component: loadView('chats/FavouriteMessage') },
      { path: '/unreadmessage', name: 'UnreadMessage', component: loadView('chats/UnreadMessage') },
      { path: '/student-application', name: 'StudentApplication', component: loadView('student/StudentApplication') },
      { path: '/student-application/:id', name: 'StudentSidebar', component: loadView('student/StudentSidebar') },
      { path: '/student-tracking', name: 'StudentTracking', component: loadView('tracking/StudentTracking') },
      { path: '/notifications', name: 'Notifications', component: loadView('notification/Notification') },
      { path: '/promotions', name: 'Promotions', component: loadView('promotions/Promotions') },
      { path: '/promotions/insights/:id', name: 'PromotionInsight', component: loadView('promotions/PromotionInsight') },
      { path: '/create-promotions', name: 'CreatePromotions', component: loadView('promotions/CreatePromotion') },
      { path: '/settings', name: 'Settings', component: loadView('settings/SettingsView') },
      { path: '/programs', name: 'Programs', component: loadView('programmes/Programmes') },
      { path: '/create/programs', name: 'CreatePrograms', component: loadView('programmes/CreateProgram') },
      { path: '/student_letter', name: 'StudentLetter', component: loadView('StudentLetter') },
      { path: '/receipt', name: 'ReceiptPage', component: loadView('payments/ReceiptsPage') },
    ],
  },
  { path: '/terms-privacy', name: 'TermsPrivacy', component: loadView('terms/Terms-privacy') },
  { path: '/forgot-password', name: 'ForgetPassword', component: loadView('password/ForgetPassword'), meta: { skipAuth: true } },
  { path: '/reset-password/:email', name: 'ResetPassword', component: loadView('password/ResetPassword'), meta: { skipAuth: true } },
  { path: '/confirm-reset-password/:email', name: 'ConfirmPasswordReset', component: loadView('password/ConfirmPasswordReset'), meta: { skipAuth: true } },
  { path: '/technical-support', name: 'TechnicalSupport', component: loadView('terms/Technical-Support') },
  { path: '/login', name: 'LogIn', component: loadView('signIn/SignIn'), meta: { skipAuth: true } },
  { path: '/email-verification', name: 'EmailOTP', component: loadView('otp/EmailOTP'), meta: { skipAuth: true } },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else {
      return { top: 0 };
    }
  },
});

// Assuming middleware is a function to be run before each route
router.beforeEach(middleware);
export default router;
